.App {
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.leaflet-container {
  width:100%;
  height:650px;
}

.App-divider {
  width: 100%;
  background-color: #222;
  border-bottom: solid 10px #CFB584;
}

.App-disclaimer-title {
  font-size: 1.5em;
  position: absolute;
  left: 60%;
  color: #f5f6fa;
  top: 0;
}

.App-disclaimer {
  font-size: 0.8em;
  position: absolute;
  left: 69%;
  color: #f5f6fa;
  top: 10px;
}



#mapid { height: 180px; }

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



